<template>

	<view class="wrapper">
		<image src="http://qp9mr1fhs.hn-bkt.clouddn.com/img/sp-bg.jpg" class="bg"></image>
		<view class="ytitle">
			<text class="ytext">商品修改</text>
		</view>
		<!-- 轮播图 -->
		<view class="whitebox">
			<view class="utitlebox">
				<view class="utitleblock"></view>
				<text class="utitle">修改轮播图</text>
				<text class="uxtitle">（最多上传6张图片）</text>
			</view>
			<view class="lbt">
				<u-upload ref="lbtload" :action="action" :auto-upload="false" max-count="6" :model="model"></u-upload>
			</view>
		</view>
		<!-- 轮播图 -->
		<connect class="connect"></connect>
		<!-- 商品信息 -->
		<view class="whitebox">
			<view class="utitlebox" style="margin-top: 20upx;">
				<view class="utitleblock"></view>
				<text class="utitle">修改商品信息</text>
			</view>
			<u-form  style="margin-left: 20upx;margin-top: 15upx;">
				<u-form-item label="商品名称:">
					<u-input placeholder="请输入商品名称" v-model="cname" />
				</u-form-item>
				<u-form-item label="生源产地:">
					<u-input placeholder="请输入生源产地" v-model="cyd" />
				</u-form-item>
				<u-form-item label="商品规格:">
					<u-input placeholder="请输入商品规格" v-model="cgg" />
				</u-form-item>
				<u-form-item label="生产日期:">
					<view @click="show = true" :style="input=='' ? 'color:#C0C4CC':'color:black'" class="scrq" style="width: 420upx;">{{ input ? input : '请选择生产日期' }}</view>
				</u-form-item>
				<u-picker mode="time" v-model="show" :params="params" @confirm="confirm"></u-picker>
				<u-form-item label="保质日期:">
					<u-input placeholder="请输入商品的保质期" v-model="cbz" />
				</u-form-item>
				<u-form-item label="商品售价:">
					<u-input placeholder="请输入商品的价格" v-model="cprice"  type="number"/>
				</u-form-item>
			</u-form>
		</view>
		<!-- 商品信息 -->
		<!-- 检测信息 -->
		<view class="whitebox">
			<view class="utitlebox" >
				<view class="utitleblock"></view>
				<text class="utitle">修改商品检测信息</text>
			</view>
			<view class="whitebox" style="width: 638upx;margin-left:-15upx ;padding: 20upx;">

				<view class="htitle">
					<text>修改检疫检测报告:</text>
					<view class="illustration" @click="ck">查看例图</view>
				</view>
				<u-mask :show="cklt" @click="qx">
					<view :class="cklt==false ? 'noseeill':'seeill'">
						<image src="http://qp9mr1fhs.hn-bkt.clouddn.com/img/lt.jpg" class="ltimg"></image>
						
					</view>
				</u-mask>
				<view class="scbg">
					<u-upload ref="jyjcload" :action="action" :auto-upload="false" max-count="1" :model="model"></u-upload>
					<u-form>
						<u-form-item label="样品名称:">
							<u-input placeholder="需与商品一致" v-model="ypname" />
						</u-form-item>
						<u-form-item label="检测日期:">
							<view @click="showjc = true" :style="inputjc=='' ? 'color:#C0C4CC':'color:black'" class="scrq" style="width: 420upx;">{{ inputjc ? inputjc : '请选择检测日期' }}</view>
						</u-form-item>
						<u-picker mode="time" v-model="showjc" :params="paramsjc" @confirm="confirmjc"></u-picker>
						<u-form-item label="检验地点:">
							<u-input placeholder="检查机构的地点" v-model="jcdd" />
						</u-form-item>
					</u-form>
				</view>
			</view>
		</view>
		<!-- 检测信息 -->
		<!-- 产品认证 -->
		<view class="whitebox">
			<view class="utitlebox" >
				<view class="utitleblock"></view>
				<text class="utitle">上传商品证书</text>
				<text class="uxtitle">(若无可以暂不上传)</text>
			</view>
			<view class="whitebox" style="width: 638upx;margin-left:-15upx ;padding: 20upx;">

				<view class="htitle">
					<text>上传商品证书:</text>
					<view class="illustration" @click="ck2">查看例图</view>
				</view>
				<u-mask :show="cklt2" @click="qx2">
					<view :class="cklt2==false ? 'noseeill':'seeill'">
						<image src="http://qp9mr1fhs.hn-bkt.clouddn.com/img/zs.jpg" class="ltimg"></image>
					
					</view>
				</u-mask>
				<view class="scbg">
					<u-upload ref="spzsload" :action="action" :auto-upload="false" max-count="9" :model="model"></u-upload>
				</view>
			</view>
		</view>
		<!-- 产品认证 -->
		<u-back-top :scroll-top="scrollTop"></u-back-top>
		<view class="btbox">
			<view class="bt1" @click="btback">返回</view>
			<view class="bt2" @click="btconfirm">确定</view>
		</view>
		<view class="aa" style="padding-bottom: 50rpx;"></view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				action: '',//后端接口地址
				scrollTop: 0,
				// 表单
				cname: '云南高山香蕉',
				cyd: '云南省丽江市滇西北高原高山农场',
				cgg: '50g',
				input: '2021-01-02',
				inputjc: '2021-01-15',
				cbz: '1个月',
				cprice: '18',
				ypname: '云南高山香蕉',
				jcdd: '云南省丽江市检疫检测技术中心',
				// 表单
				show: false,
				showjc: false,
				mode: 'time',
				cklt: false,
				cklt2: false,
				params: {
					year: true,
					month: true,
					day: true,
					hour: false,
					minute: false,
					econd: false
				},
				paramsjc: {
					year: true,
					month: true,
					day: true,
					hour: false,
					minute: false,
					econd: false
				},
				year: 0,
				month: 0,
				day: 0,
			}
		},
		onLoad() {

		},
		onPageScroll(e) {
				this.scrollTop = e.scrollTop;
		},
		methods: {
			submit() {
				this.$refs.uUpload.upload();
			},
			aa() {
				console.log("aa")
				this.show = true
			},
			ck() {
				this.cklt = true
			},
			qx() {
				this.cklt = false
			},
			ck2() {
				this.cklt2 = true
			},
			qx2() {
				this.cklt2 = false
			},
			confirm(e) {
				this.input = '';
				if (this.mode == 'time') {
					if (this.params.year) this.input += e.year;
					if (this.params.month) this.input += '-' + e.month;
					if (this.params.day) this.input += '-' + e.day;
					if (this.params.hour) this.input += ' ' + e.hour;
					if (this.params.minute) this.input += ':' + e.minute;
					if (this.params.second) this.input += ':' + e.second;
				} else if (this.mode == 'region') {
					this.input = e.province.label + '-' + e.city.label + '-' + e.area.label;
				} else if (this.mode == 'selector') {
					this.input = this.range[e[0]];
				} else if (this.mode == 'multiSelector') {
					this.input = this.range[0][e[0]] + '-' + this.range[1][e[1]] + '-' + this.range[2][e[2]];
				}
			},
			confirmjc(e) {
				this.inputjc = '';
				if (this.mode == 'time') {
					if (this.paramsjc.year) this.inputjc += e.year;
					if (this.paramsjc.month) this.inputjc += '-' + e.month;
					if (this.paramsjc.day) this.inputjc += '-' + e.day;
					if (this.paramsjc.hour) this.inputjc += ' ' + e.hour;
					if (this.paramsjc.minute) this.inputjc += ':' + e.minute;
					if (this.paramsjc.second) this.inputjc += ':' + e.second;
				} else if (this.mode == 'region') {
					this.input = e.province.label + '-' + e.city.label + '-' + e.area.label;
				} else if (this.mode == 'selector') {
					this.input = this.range[e[0]];
				} else if (this.mode == 'multiSelector') {
					this.input = this.range[0][e[0]] + '-' + this.range[1][e[1]] + '-' + this.range[2][e[2]];
				}
			},
			btconfirm() {
				let re = /[^\u4e00-\u9fa5]/;
				let time = new Date();
				this.year = time.getFullYear();
				this.month = Number(time.getMonth()) + 1;
				this.day = time.getDate();
		
				if(this.cname == '' || this.cyd == '' || this.cgg == '' || this.input == '' || this.inputjc == '' || this.cbz == '' ||  this.cprice == '' || this.ypname == ''  || this.jcdd == ''){
					uni.showToast({
						title:'请将信息输入完整',
						icon:'none'
					})
				}
				
				else if(this.$refs.lbtload.lists.length == 0 ){
					uni.showToast({
						title:'请添加轮播图',
						icon:'none'
					})					
				}
				else if(this.input.slice(0,4) == this.year && this.input.slice(5,7)>this.month){
						uni.showToast({
							title:'生产日期有误',
							icon:'none'
						})				
				}
				else if(this.input.slice(0,4) == this.year && this.input.slice(5,7)>this.month && this.input.slice(8,10)>this.day){
					uni.showToast({
						title:'生产日期有误',
						icon:'none'
					})					
				}
				else if(this.inputjc.slice(0,4) == this.year && this.input.slice(5,7)>this.month){
						uni.showToast({
							title:'检测日期有误',
							icon:'none'
						})				
				}
				else if(this.inputjc.slice(0,4) == this.year && this.input.slice(5,7)>this.month && this.input.slice(8,10)>this.day){
					uni.showToast({
						title:'检测日期有误',
						icon:'none'
					})					
				}
				else if(this.$refs.jyjcload.lists.length == 0 ){
					uni.showToast({
						title:'请添加检疫检测报告',
						icon:'none'
					})					
				}
				
				else if (re.test(this.cname)){
					uni.showToast({
						title:'商品名称输入有误',
						icon:'none'
					})
				}
				else{
					uni.showModal({
						title: '提示',
						content: '确定修改商品无误',
						confirmColor: '#0ABB9A',
						success: function(res) {
							if (res.confirm) {
								console.log('用户点击确定');
								uni.navigateTo({
									url: 'Product-list',
									success: res => {},
									fail: () => {},
									complete: () => {}
								});
							} else if (res.cancel) {
								console.log('用户点击取消');
							}
						}
					});
				}
				
			},
			btback() {
				uni.navigateTo({
					url: 'Product-list'
				})
			}

		}
	}
</script>

<style>
	page {
		background-color: transparent;
	}

	.bg {
		position: absolute;
		z-index: -1;

		left: 0;
		width: 100%;
		height: 1300upx;

	}

	.whitebox:first-child {
		padding-bottom: 60upx;
	}

	.lbt {
		margin-top: 20upx;
		box-shadow: 0 0 5upx #C0C4CC;
		border-radius: 30upx;
		padding: 5upx;
		margin-bottom: 20upx;
	}

	.scrq {
		font-size: 28upx;
	}

	.htitle {
		display: flex;
		margin-left: 20upx;
	}

	.htitle text {
		font-weight: bold;
		font-size: 30upx;
		margin-top: 5upx;
	}

	.illustration {
		width: 130upx;
		height: 50upx;
		background-color: #0ABB9A;
		margin-left: 20upx;
		color: white;
		font-size: 20upx;
		line-height: 50upx;
		text-align: center;
		border-radius: 15upx;
	}

	.scbg {

		margin-top: 20upx;
	}

	.noseeill {
		display: none;
	}

	.seeill {
		display: flex;
		align-items: center;
		justify-content: center;
		height: 100%;
	}

	.ltimg {
		height: 830upx;
		width: 640upx;
	}

	.seeill .qx {
		height: 80upx;
		width: 80upx;
		position: absolute;
		top: 82%;
	}

	.qxbt {
		height: 80upx;
		width: 80upx;
		margin-top: 10upx;
	}
</style>
